const uni = require('uni').default;








































export default {
	data() {
		return {
			details: {
				id: 1,
				icon: '',
				company: '',
				startTime: '',
				joins: '',
				settingReminders: ''
			},
			isState: false
		}
	},
	onLoad: function(option) {
		uni.showToast({
			title: `groupId:${option.groupId},id:${option.listId}`,
			mask: true,
			duration: 1500
		})
	},
	methods: {
		onClickChangeState() {
			
			this.isState = !this.isState;
			uni.showToast({
				title: this.isState ? `已参加` : `已取消`,
				mask: true,
				duration: 1500
			})
		},
		onClickGoError(){
			uni.navigateTo({
				url:`/pages/home/wcalendar/errorCorrection`
			})
		}
	}
}



